  top: 350px;
}

#app4 {
  width: 300px;
  height: 300px;
  position: absolute;
  left: 350px;
  top: 350px;
}

#app {
  position: absolute;
  width: 600px;
  height: 300px;
  border: 1px dashed #4a4a5a;
  cursor: crosshair;

  /*Center the chart for resize debugging purposes*/
  top: 50%;
  left: 50%;
  width: 75%;
  height: 50%;
  transform: translate(-50%, -100%);
  -webkit-transform: translate(-50%, -100%);

  display: block;
  /* overflow:hidden; */
}

canvas {
  display: block;
}
